.card-container {
  position: relative;
  width: 400px;
  height: 550px;
  margin: 60px auto;
  /* perspective: 1000px; */
  /* 3D       */
}

.card {
  position: absolute;
  width: 300px;
  height: 500px;
  padding: 20px;
  border: 1px solid;
  border-radius: 10px;
  box-shadow: 2px 3px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.6s ease;
  transform-style: preserve-3d;
  /* Y  (rotateY)   */
  opacity: 0.85;
}

.card:hover {
  box-shadow: 6px 10px 15px rgba(0, 0, 0, 0.2);
  /*     ,    */
  transform: scale(1.02) translateY(-5px) rotateY(0deg);
}

.card.active {
  opacity: 1;
  /*   :   */
}

.card-content {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.4s ease;
  /*      */
}

.card.active .card-content {
  opacity: 1;
  transform: translateY(0);
  /* active     (opacity: 1) */
}

.card-icon {
  margin-bottom: 10px;
  color: #333;
}

.tab-bar {
  display: flex;
  justify-content: center;
  margin-top: 520px;
  gap: 12px;
}

.tab-btn {
  background: #f0f0f0;
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
}

.tab-btn.selected {
  background: #333;
  color: white;
  /*    (selected ) */

}
